  function concatBuffer(bufs) {//@ts-ignore
    var len = bufs.reduce(function(a, b) { return a.length + b.length })
      , buf = new Buffer(len)
      , start = 0;
  //@ts-ignore
    bufs.forEach(function(chunk) {
      chunk.copy(buf, start, 0);
      start += chunk.length;
    });
  
    return buf;
  }


